@import "../../libs/include-media/dist/include-media";
@import "../utils/special-version-variables";
@import "../utils/mixins";

// mixin
@mixin btn-nav() {
	display: inline-block;
	vertical-align: top;
	width: auto;
	height: auto;
	font: {
		family: inherit;
		weight: normal;
		style: normal; }
	@include spec-font-size--item;
	//font-size: inherit
	//line-height: inherit
	padding: 0.3em 0.6em;
	cursor: pointer;
	letter-spacing: inherit;
	text-transform: none;
	transition: none;
	text-decoration: none;
	border: 2px solid currentColor;
	border-radius: 0.5em;
	@include spec-reset-bg;

	&::before, &::after {
		display: none; } }

@mixin btn-nav-hover() {
	@include spec-reset-bg-invert; }

@mixin btn-nav-current() {
	font-weight: bold;
	background: none; }

.#{$spec-mod} {
	// buttons default
	// ---------------------------------------------------------------------------
	.btn-default,
	.btn-default-alt,
	.btn-submit,
	.btn-reset,
	.bx-newslist .btn {
		position: relative;
		display: inline-block;
		vertical-align: top;
		width: auto;
		height: auto;
		font: {
			family: inherit;
			weight: normal;
			style: normal;
			size: inherit; }
		line-height: round-float(20px / 18px, 1);
		padding: 10px 18px;
		cursor: pointer;
		letter-spacing: inherit;
		text-transform: uppercase;
		transition: none;
		text-decoration: none;
		border: 2px solid currentColor;
		border-radius: 10px;
		@include spec-reset-bg-invert;

		&::before, &::after {
			display: none; }

		&:hover, &:focus {
			//+spec-reset-bg
			background: none;
			color: inherit;
			border-color: inherit; } }

