/* ============================================================ */

/* to top */
.#{$spec-mod} {
  .btn-to-top {
    opacity: 0.5;

    i {
      &::before, &::after {
        background-color: currentColor; } }

    .no-touchevents &:hover {
      opacity: 0.9; } } }

/* to top end */

.#{$spec-mod} {
  .full-page {
    h1, h2, h3, h4, h5, h6 {
      max-width: none; } }

  // sidebar
  .sidebar {
    width: auto !important; }

  // header
  .header-layout {
    text-align: center; }

  .logo {
    position: relative;
    // важно
    left: auto;
    top: auto;
    display: inline-block;
    vertical-align: top;
    width: auto;
    height: auto;
    background: none;
    border: none;
    z-index: auto;
    box-sizing: border-box;
    margin-bottom: 40px;

    @include media('<mob-lg') {
      text-align: center; }

    a {
      height: auto;
      padding: 0;
      display: inline-block;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      text-align: left;

      @include media('<mob-lg') {
        display: inline-block;
        vertical-align: top; } }

    img, .logo__text {
      display: inline-block;
      margin: 0;

      @include media('<mob-lg') {
        display: block;
        text-align: center; } }

    img {
      margin-right: 1em;

      @include media('<mob-lg') {
        margin: 0 auto; } }

    .logo__text {
      left: auto;
      top: auto;
      opacity: 1;
      font-family: inherit;
      font-weight: bold;
      font-size: 26px;
      line-height: 34px;
      color: currentColor;
      letter-spacing: normal;
      word-spacing: normal; } }

  // nav
  .nav-list {
    max-height: none !important;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-wrap: wrap;

    @include media('<mob-lg') {
      font-size: 16px;
      line-height: 20px;
      letter-spacing: normal;
      word-spacing: normal; }

    li {
      display: inline-block;
      margin: 0.3em 0.6em 0.3em 0; }

    a {
      -webkit-tap-highlight-color: transparent;
      user-select: none;

      &::before, &::after {
        display: none; } } }

  .main {
    $space: $spec-bottom-space-item;
    padding-top: $space;
    padding-bottom: $space; }

  //+media('<desk-ml')
  //$space: 2.5em
  //padding-top: $space
  //padding-bottom: $space

  //+media('<tab-sm')
  //$space: 2em
  //padding-top: $space
  //padding-bottom: $space

  .content {
    background: none; }

  .wrapper {
    min-height: 0;
    height: auto;
    overflow-y: visible;
    overflow-x: hidden;
    z-index: 6; }

  .footer {
    border-top: $spec-border-width solid currentColor;
    padding: 20px 0; }

  // mobile menu switcher
  .btn-menu {
    display: none !important; }

  a > span {
    border-bottom: none !important; }

  // icon
  [class*="icon-"] {
    font-style: normal; }

  .datetime {
    height: auto; }

  // breadcrumbs
  .breadcrumbs {
    @include spec-font-size--note; }

  .breadcrumbs-list {
    li, a, span {
      display: inline;
      vertical-align: baseline;
      line-height: inherit;
      white-space: normal; }

    li {
      padding: 0;
      margin: 0 0.4em 0 0;

      &::before {
        color: inherit; }

      &, &:first-child {
        &::before {
          position: relative;
          left: auto;
          top: auto;
          display: inline;
          background: none;
          width: auto;
          height: auto;
          border: none;
          margin: 0 0.4em 0 0; } }

      &:last-child {
        & > span {
          display: inline;
          white-space: normal;
          max-width: none;
          overflow: visible;
          text-overflow: unset; } } }

    a {
      &:hover, &:focus {
        color: inherit; } } }

  // user form
  .user-form {
    background: none;

    .input-wrap {
      &::before, &:after {
        display: none; } }

    .label-holder {
      float: none;
      width: auto;
      padding: 0;
      margin-bottom: 3px;

      &::before {
        display: none; }

      label, .label {
        max-width: none;
        display: inline;
        vertical-align: initial; } }

    .input-holder {
      margin-left: 0; }

    .form-footer {
      margin-left: 0;
      padding-left: 0; }

    .form-buttons {
      float: none;
      margin-right: 0;
      margin-bottom: $spec-bottom-space-element;

      &:last-child {
        margin-bottom: 0; }

      .btn-submit {
        width: auto; } }

    .form-note {
      height: auto;
      margin-bottom: $spec-bottom-space-element;

      &:last-child {
        margin-bottom: 0; }

      &::before {
        display: none; } }

    .form-note__item {
      width: auto;
      display: block; } }

