  .jFiler {
    color: inherit; }

  .jFiler-input-dragDrop {
    @include spec-reset-bg;
    border: $spec-border-width dashed currentColor;
    color: currentColor; }

  .jFiler-input-icon {
    display: none !important; }

  .jFiler-input-text {
