      font-weight: bold;
      background: none; } }

  // files
  .file {
    display: inline-block;
    vertical-align: top;
    height: auto !important;
    text-decoration: none;

    [class*="depict-files-"] {
      //float: left
      display: inline-block;
      vertical-align: middle;
      position: relative;
      left: auto;
      top: auto;
      //margin: 0.5em 20px 0 0
      margin: 0 10px 0 0; }

    .file__content {
      //overflow: hidden
      overflow: visible;
      display: inline-block;
      vertical-align: middle; }

    .file__title,
    .file__info {
      display: inline;
      text-decoration: underline; }

    .file__info {
      font-style: italic;
      font-weight: bold;

      &::before, &::after {
        display: inline; }

      &::before {
        content: '('; }

      &::after {
        content: ')'; } }

    &:hover {
      [class*="depict-files-"],
      [class*=" depict-files-"] {
        &::before {
          opacity: 1; }

        &::after {
          display: none; } }

