      .file__info {
        color: inherit;
        text-decoration: none; } } }

  // catalog menu
  $catalog-menu-border-width: 2px;

  .catalog-menu {
    background: none;
    box-sizing: border-box;

    &::after {
      content: none; } }

  .catalog-menu-list {
    width: 100%;
    box-shadow: none;
    visibility: visible;
    word-wrap: break-word;
    //box-shadow: inset 0 0 0 $catalog-menu-border-width currentColor
    //padding: round($catalog-menu-border-width/2)
    padding: 1em;
    border: $catalog-menu-border-width solid currentColor;

    li {
      padding: 0;
      transition: none;
      box-sizing: border-box;
      float: none;
      display: inline;
      width: auto;
      margin: 0 1em 0 0;

      &::before, &::after {
        border-color: currentColor;
        display: none; } }

    a {
      display: inline;
      box-shadow: none;
      box-sizing: border-box;
      height: auto;
      padding: 0;
      @include spec-font-reset;
      //box-shadow: 0 0 0 round($catalog-menu-border-width/2) currentColor
      //border: round($catalog-menu-border-width/2) solid currentColor

      &::before {
        content: none; }

      &:hover {
        color: currentColor !important;
        background: none !important;
        //box-shadow:  0 0 0 round($catalog-menu-border-width/2) currentColor, inset 0 0 0 $catalog-menu-border-width currentColor
        box-shadow: none; }

      span {
        display: inline;
        vertical-align: baseline;
        max-width: none; } }

    .has-submenu {
      display: block;
      margin: 1em 0 0 0;

      &:first-child {
        margin-top: 0; }

      & > a {
        color: currentColor;
        font-family: inherit;
        font-size: inherit;
        line-height: inherit;
        text-transform: uppercase; }

      ul {
        margin: 0;

        &::before, &::after {
          display: none; }

        a {
          padding-left: 0;

          &::after {
            border-color: currentColor;
            display: none; } } } } }

