  // form
  .unif-form {
    .field.error {
      &, & .uploader .action {
        background: none; } }

    .errors {
      padding: 0 0.5em;
      font-weight: bold;
      font-style: italic; } }

  .btn-submit,
  .btn-reset {
    & > span {
      opacity: 1 !important;
      max-width: none !important; } }

  // tabs
  .r-tabs-panel {
    display: block !important;
    opacity: 1 !important; }

  .r-tabs-anchor {
    text-decoration: none;
    cursor: default; }

  // faq, terminals
  .faq-item, .terminals-item {
    position: relative;
    border: 0 solid currentColor;
    border-bottom-width: $spec-border-width;

    &:first-child {
      border-top-width: $spec-border-width; }

    h3 {
      max-height: none;
      height: auto;
