      background: none;
      box-shadow: none;

      .terminals-item__holder {
        box-shadow: none;
        transition: none; } }

    .faq-item__angle, .terminals-item__angle {
      top: 1.5em;
      right: 0;
      left: auto;
      cursor: pointer;

      &::before, &::after {
        background-color: currentColor !important;
        border-color: currentColor !important; } }

    .terminals-item__angle {
      top: 1.3em; } }

  .heading + .default-section .title-box {
    padding: 0; }

  // news articles
  .news-article {
    a {
      min-height: 0 !important;
      text-decoration: none; }

    h3 {
      min-height: 0;
      transition: none;
      text-decoration: underline; }

    a:hover {
      time, h3, p {
        color: inherit;
        text-decoration: none; } }

    figure img {
      position: relative;
      left: auto;
      top: auto;
      width: 320px;
      max-width: 100%;
      margin: 0; } }

  // advantages
