  .easy-item a:hover .easy-text {
    transform: none;
    transition: none; }

  // search
  .multi-search__field {
    input[type="text"] {
      margin: 0;
      padding-right: 45px;
      font-weight: bold;

      &:hover, &:focus {
        border-color: currentColor; } }

    i {
      &::before {
        border-color: currentColor; }

      &::after {
        background-color: currentColor; } } }

  // .ui-autocomplete
  .ui-autocomplete {
    border-color: currentColor;
    @include spec-reset-bg;
    @include spec-font-reset;
    box-shadow: none; }

  .ui-menu-item {
    @include spec-font-reset;
