@import "../../libs/include-media/dist/include-media";
@import "../utils/special-version-variables";
@import "../utils/mixins";

.#{$spec-mod} {

	// vertical space - section
	// ---------------------------------------------------------------------------
	.about-section,
	.movie-section,
	.docs-section,
	.call-section,
	.callback-section,
	.faq-list,
	.default-section,
	.news-section,
	.faq-section,
	.advantages-section,
	.helpful-section,
	.cells-list,
	.terminals,
	.terminals-more,
	.s-points,
	.info-section,
	.bx-newslist,
	.spec-bottom-space-section,
	.spec-bottom-space-section {
		margin-bottom: $spec-bottom-space-section;

		&:last-child {
			margin-bottom: 0; } }

	// vertical space - item
	// ---------------------------------------------------------------------------
	.breadcrumbs,
	.movie,
	.movie__item,
	.tabs-panel,
	.tabs-container .tabs-panel,
	.callback,
	.tabs-container,
	.r-tabs,
	//.faq-item,
	//.terminals-item,
	.news-article,
	.advantages,
	.advantages-item,
	.helpful,
	.s-points__filters,
	.info,
	.info-row,
	.info-item,
	.easy,
	.agency,
	.gallery-zoom,
	.gallery-zoom figure,
	.bx-newslist-container,
	.align-layout,
	.spec-bottom-space-item,
	.spec-bottom-space-item {
		margin-bottom: $spec-bottom-space-item;

		&:last-child {
			margin-bottom: 0; } }

	// vertical space - default
	// ---------------------------------------------------------------------------
	h1, h2, h3, h4, h5, h6,
	p, hr, blockquote,
	blockquote q,
	blockquote > p,
	.files,
	.files .files-item,
	.title-box,
	.section-note,
	.about-info,
	.about-info__col,
	.section-footer,
	.call-item,
	.call-description,
	.text-layout,
	.blockquote,
	.system-nav-orange,
	.content-widget,
	.form-row,
	.input-wrap,
	.r-tabs-accordion-title,
	.attention,
	.form-buttons-col,
	.s-points__filters-item,
	.cells-list > li,
	.spec-bottom-space,
	.spec-bottom-space {
		margin-bottom: $spec-bottom-space;

		&:last-child {
			margin-bottom: 0; } }

	// vertical space - element
	// ---------------------------------------------------------------------------
	.movie__video,
	.movie__text,
	.nav-title,
	.nav-pages,
	.news-article figure,
	.news-article footer,
	.advantages-img,
	.helpful__note,
	.spec-bottom-space-element,
	.spec-bottom-space-element {
		margin-bottom: $spec-bottom-space-element;

		&:last-child {
			margin-bottom: 0; } } }
